@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
*{font-family:"Roboto",sans-serif}body{background-color:#737373}.conteiner{max-width:1280px;margin:0 auto}
.home{background-image:linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url(/starit/static/media/home-bg.a07e0f57.jpg);background-size:cover;height:800px;padding:0 40px}.home-header{display:flex;align-items:center}.home-header-item{margin-left:60px}.home-header-item:first-child{margin-left:0}.home-navigation{margin-top:25px;position:fixed}.home-navigation a:hover{border-bottom:3px solid #ebac00;color:#ebac00}.home-navigation-item{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;text-transform:uppercase;color:#fff;margin-left:74px}.home-navigation-item:first-child{margin-left:0}.home-title{width:500px;font-weight:bold;font-size:48px;line-height:55px;color:#fff;margin:65px 0 280px}.home-link{display:inline-block;padding:25px 100px;text-decoration:none;font-weight:normal;font-size:32px;line-height:37px;color:#fff;background:#b78a3f}
